// swiper
// eslint-disable-next-line no-new, no-undef
let mySwiper = new Swiper('#reviews-slider', {
  direction: 'horizontal',
  navigation: {
    nextEl: '.swiper-button-next',
    prevEl: '.swiper-button-prev',
  },
});

mySwiper.on('slideChange', function () {
  //   ""   
  if (mySwiper.activeIndex === 0) {
    mySwiper.navigation.$prevEl.addClass('swiper-button-disabled');
  } else {
    mySwiper.navigation.$prevEl.removeClass('swiper-button-disabled');
  }

  //   ""   
  if (mySwiper.activeIndex === mySwiper.slides.length - 1) {
    mySwiper.navigation.$nextEl.addClass('swiper-button-disabled');
  } else {
    mySwiper.navigation.$nextEl.removeClass('swiper-button-disabled');
  }
});

const initTrainersSlider = () => {
  // eslint-disable-next-line no-unused-vars, no-undef, no-new
  new Swiper('#trainers-slaider', {

    slidesPerView: 4,
    spaceBetween: 30,

    breakpoints: {
      // when window width is >= 320px
      320: {
        slidesPerView: 1,
        spaceBetween: 0,
      },
      // when window width is >= 480px
      768: {
        slidesPerView: 2,
        spaceBetween: 30,
      },
      // when window width is >= 640px
      1200: {
        slidesPerView: 4,
        spaceBetween: 40,
      },
    },

    direction: 'horizontal',
    loop: true,

    navigation: {
      nextEl: '.swiper-button-next',
      prevEl: '.swiper-button-prev',
    },
  });
};
initTrainersSlider();

//   
function validateName(name) {
  if (name.trim() === '') {
    return ' ';
  }
  return '';
}

//     
function validatePhone(phone) {
  if (phone.trim() === '') {
    return ' ';
  }
  return '';
}

//     
function updateErrorLabel(labelElement, errorMessage) {
  labelElement.textContent = errorMessage;
  if (errorMessage) {
    labelElement.style.color = 'red';
    labelElement.style.marginTop = '34px';
  } else {
    labelElement.style.color = '';
    labelElement.style.marginTop = '';
  }
}

//  
const form = document.querySelector('.feedback-form__data');

//     
const nameInput = form.querySelector('input[name="name"]');
const phoneInput = form.querySelector('input[name="phone"]');
const nameLabel = form.querySelector('.form-name');
const phoneLabel = form.querySelector('.form-phone');

//    
form.addEventListener('submit', function (event) {
  event.preventDefault();

  //       
  const nameError = validateName(nameInput.value);
  updateErrorLabel(nameLabel, nameError);

  //       
  const phoneError = validatePhone(phoneInput.value);
  updateErrorLabel(phoneLabel, phoneError);

  //   ,  
  if (!nameError && !phoneError) {
    form.reset();
  }
});

//       
const submitButton = form.querySelector('.btn');
submitButton.addEventListener('click', function () {
  form.dispatchEvent(new Event('submit'));
});
